@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400;1,700&family=Quicksand:wght@400;500;700&display=swap");
* {
  margin: 0;
  padding: 0;
}
body {
  color: gray;
  background: #f1f1f1;
  font-family: "Quicksand", Cambria, Cochin, Georgia, Times, "Times New Roman";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
